<template>
    <v-container class="d-flex justify-center mt-15">  

        <form enctype="text/plain" @submit.prevent="verificar">
        <v-card :loading="loading" :disabled="loading" 
        outlined height="auto" max-width="350" >
            <v-card-title class="text-h3" primary-title>
                Entrada
            </v-card-title>
            <v-card-text>
                <v-row no-gutters>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="text"
                        :rules="nomeRegra"
                        v-model="form.nome"
                        label="usuário"
                        prepend-icon="mdi-account"
                        hint="Informe o usuário">
                        </v-text-field>
                    </v-col>

                    <v-col cols="12" sm="12">
                        <v-text-field 
                        type="password"
                        :rules="passwordRegra"
                        v-model="form.senha"
                        :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                        :type="show1 ? 'text' : 'password'"
                        @click:append="show1 = !show1"
                        label="Palavra-passe"
                        prepend-icon="mdi-lock"
                        hint="Informe a Palavra-passe">
                        </v-text-field>                        
                    </v-col>

                </v-row>
                    

            </v-card-text>
            <v-card-actions>
                <v-btn color="success" type="submit" > entrar </v-btn>
                <v-spacer></v-spacer>
                <v-btn :to="{ name:'cadastro' }" color="primary"> registrar-se </v-btn>
                
            </v-card-actions>                
        </v-card> 
        </form>
</v-container>
  </template>
  
  <script>
    import { mapActions, mapGetters } from 'vuex'
  export default {
    data(){
        return{
            form:{
                nome:'',
                senha:''
            },
            loading:false,
            show1:false,
                valid:true,
                 nomeRegra: [
                    v => !!v || 'Informe o nome de usuário!',
                ],
                 passwordRegra: [
                    v => !!v || 'Informe a palavra-passe do usuário!',
                ],
        } 
        
    },
    methods:{
        ...mapActions({
            autenticar:'usuario/autenticar'

            }),
        async verificar(){
            try {
                this.loading = true
                
                
                await this.autenticar(this.form)

                this.$router.push({ name: 'home' })
                //alert('Acesso liberado')
                
                this.loading = false

            } catch (error) {

                this.loading = false

                    alert(error)

            }

        }

    },
    mounted() {
      //  Document.cookie = "id=1"

      //  this.$router.push({ name: 'home' })

    },
    
  }
  </script>